* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}

/* =========================
   THEME
========================= */
.app.dark {
  background-color: #0f172a;
  color: #e5e7eb;
}

.app.light {
  background-color: #f8fafc;
  color: #0f172a;
}

/* =========================
   THEME TOGGLE
========================= */
.theme-toggle {
  position: fixed;
  top: 20px;
  right: 20px;
  border: none;
  background: #3b82f6;
  color: white;
  border-radius: 50%;
  width: 44px;
  height: 44px;
  cursor: pointer;
  font-size: 18px;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.25);
  z-index: 100;
}

/* =========================
   HERO SECTION
========================= */
.hero {
  padding: 80px 24px;
  text-align: center;
}

.hero h1 {
  font-size: 3rem;
  margin: 16px 0 8px;
}

.hero p {
  opacity: 0.85;
  font-size: 1.1rem;
}

/* =========================
   PROFILE IMAGE
========================= */
.profile-img {
  width: 160px;
  height: 160px;
  border-radius: 50%;
  object-fit: cover;
  border: 4px solid #3b82f6;
}

/* =========================
   SECTIONS
========================= */
.section {
  max-width: 900px;
  margin: auto;
  padding: 60px 24px;
}

.section.alt {
  background: rgba(255, 255, 255, 0.03);
}

.app.light .section.alt {
  background: rgba(0, 0, 0, 0.03);
}

.section h2 {
  font-size: 2rem;
  margin-bottom: 24px;
}

/* =========================
   SKILLS
========================= */
.skills-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
}

.skill-pill {
  padding: 10px 16px;
  border-radius: 999px;
  border: 1px solid #3b82f6;
  background: rgba(59, 130, 246, 0.15);
  font-size: 14px;
}

/* =========================
   ACHIEVEMENTS
========================= */
.achievements {
  list-style: none;
  padding-left: 0;
}

.achievements li {
  margin-bottom: 14px;
  padding-left: 20px;
  position: relative;
}

.achievements li::before {
  content: "âœ”";
  position: absolute;
  left: 0;
  color: #3b82f6;
}

/* =========================
   EXPERIENCE TIMELINE
========================= */
.timeline {
  position: relative;
  margin-top: 40px;
  padding: 0 10px;
}

/* Vertical line */
.timeline::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 0;
  bottom: 0;
  width: 2px;
  background: #3b82f6;
  transform: translateX(-50%);
}

/* Timeline item */
.timeline-item {
  position: relative;
  width: 50%;
  padding: 20px 30px;
}

/* Left alignment */
.timeline-item.left {
  left: 0;
  text-align: right;
}

/* Right alignment */
.timeline-item.right {
  left: 50%;
  text-align: left;
}

/* Timeline dot */
.timeline-dot {
  position: absolute;
  top: 32px;
  width: 14px;
  height: 14px;
  background: #3b82f6;
  border-radius: 50%;
  z-index: 2;
}

/* Dot positioning */
.timeline-item.left .timeline-dot {
  right: -7px;
}

.timeline-item.right .timeline-dot {
  left: -7px;
}

/* Timeline card */
.timeline-card {
  background: #020617;
  padding: 18px;
  border-radius: 12px;
  border-left: 4px solid #3b82f6;
}

.app.light .timeline-card {
  background: #ffffff;
}

/* Timeline text */
.timeline-card p {
  margin: 6px 0;
  opacity: 0.8;
}

.timeline-card span {
  font-size: 13px;
  opacity: 0.6;
}

/* =========================
   FOOTER
========================= */
.footer {
  text-align: center;
  padding: 40px 20px;
  opacity: 0.85;
}

.contact-actions {
  margin-top: 16px;
  display: flex;
  justify-content: center;
  gap: 16px;
}

.contact-btn {
  padding: 10px 22px;
  border-radius: 999px;
  background: #3b82f6;
  color: white;
  text-decoration: none;
  font-size: 14px;
}

.contact-btn:hover {
  box-shadow: 0 8px 20px rgba(59, 130, 246, 0.4);
}

.contact-link {
  color: #3b82f6;
  text-decoration: none;
  font-size: 14px;
}

.contact-link:hover {
  text-decoration: underline;
}

/* =========================
   MOBILE RESPONSIVE
========================= */
@media (max-width: 768px) {
  .timeline::before {
    left: 10px;
  }

  .timeline-item {
    width: 100%;
    padding-left: 40px;
    padding-right: 0;
    text-align: left;
  }

  .timeline-item.left,
  .timeline-item.right {
    left: 0;
  }

  .timeline-dot {
    left: 3px;
    right: auto;
  }
}
